import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { VarsService } from 'src/app/services/vars.service'

@Injectable({
	providedIn: 'root'
 })
 
export class ApiService {
	constructor(
		private http: HttpClient,
		private vars: VarsService
		) { }

	dbUrl = this.vars.dbUrl;

	httpOptions = {
		headers: new HttpHeaders({
			'Content-Type':  'application/x-www-form-urlencoded'
		})
	};

	getInfoUser() {
		return this.http.get(
			'https://ipapi.co/json/'
		)
	};

	login(json) {
		return this.http.post(
			this.dbUrl + 'login', 
			"json="+JSON.stringify(json), 
			this.httpOptions
		)
	};

	//Usuarios
	listUsuarios(token) {
		return this.http.post(
			this.dbUrl + 'usuarios/list', 
			"authorization=" + token,
			this.httpOptions
		)
	};

	viewUsuario(id, token) {
		return this.http.post(
			this.dbUrl + 'usuarios/view/' + id, 
			"authorization=" + token,
			this.httpOptions
		)
	};

	createUsuario(json, token) {
		return this.http.post(
			this.dbUrl + 'usuarios/create', 
			"json="+JSON.stringify(json) + "&authorization=" + token,
			this.httpOptions
		)
	};

	editUsuario(json, token) {
		return this.http.post(
			this.dbUrl + 'usuarios/edit', 
			"json="+JSON.stringify(json) + "&authorization=" + token,
			this.httpOptions
		)
	};

	deleteUsuario(id, token) {
		return this.http.post(
			this.dbUrl + 'usuarios/delete/' + id, 
			"authorization=" + token,
			this.httpOptions
		)
	};
	//Empresas
	listEmpresas(token) {
		return this.http.post(
			this.dbUrl + 'empresas/list', 
			"authorization=" + token,
			this.httpOptions
		)
	};

	viewEmpresa(id, token) {
		return this.http.post(
			this.dbUrl + 'empresas/view/' + id, 
			"authorization=" + token,
			this.httpOptions
		)
	};

	createEmpresa(json, token) {
		return this.http.post(
			this.dbUrl + 'empresas/create', 
			"json="+JSON.stringify(json) + "&authorization=" + token,
			this.httpOptions
		)
	};

	editEmpresa(json, token) {
		return this.http.post(
			this.dbUrl + 'empresas/edit', 
			"json="+JSON.stringify(json) + "&authorization=" + token,
			this.httpOptions
		)
	};

	deleteEmpresa(id, token) {
		return this.http.post(
			this.dbUrl + 'empresas/delete/' + id, 
			"authorization=" + token,
			this.httpOptions
		)
	};
	//Direcciones
	listDireccionesByEmpresa(idEmpresa,token) {
		return this.http.post(
			this.dbUrl + 'direcciones/listbyempresa/' + idEmpresa, 
			"authorization=" + token,
			this.httpOptions
		)
	};

	viewDireccion(id, token) {
		return this.http.post(
			this.dbUrl + 'direcciones/view/' + id, 
			"authorization=" + token,
			this.httpOptions
		)
	};

	createDireccion(json, token) {
		return this.http.post(
			this.dbUrl + 'direcciones/create', 
			"json="+JSON.stringify(json) + "&authorization=" + token,
			this.httpOptions
		)
	};

	editDireccion(json, token) {
		return this.http.post(
			this.dbUrl + 'direcciones/edit', 
			"json="+JSON.stringify(json) + "&authorization=" + token,
			this.httpOptions
		)
	};

	deleteDireccion(id, token) {
		return this.http.post(
			this.dbUrl + 'direcciones/delete/' + id, 
			"authorization=" + token,
			this.httpOptions
		)
	};
	//Cupones
	listCuponesByEmpresa(idEmpresa, token) {
		return this.http.post(
			this.dbUrl + 'cupones/listbyempresa/' + idEmpresa , 
			"authorization=" + token,
			this.httpOptions
		)
	};

	viewCupon(id, token) {
		return this.http.post(
			this.dbUrl + 'cupones/view/' + id, 
			"authorization=" + token,
			this.httpOptions
		)
	};

	createCupon(json, token) {
		return this.http.post(
			this.dbUrl + 'cupones/create', 
			"json="+JSON.stringify(json) + "&authorization=" + token,
			this.httpOptions
		)
	};

	editCupon(json, token) {
		return this.http.post(
			this.dbUrl + 'cupones/edit', 
			"json="+JSON.stringify(json) + "&authorization=" + token,
			this.httpOptions
		)
	};

	
	reorderCupon(json, token) {
		return this.http.post(
			this.dbUrl + 'cupones/reorder', 
			"json="+JSON.stringify(json) + "&authorization=" + token,
			this.httpOptions
		)
	};

	deleteCupon(id, token) {
		return this.http.post(
			this.dbUrl + 'cupones/delete/' + id, 
			"authorization=" + token,
			this.httpOptions
		)
	};
	
	//Categorias
	listCategorias(token) {
		return this.http.post(
			this.dbUrl + 'categorias/list', 
			"authorization=" + token,
			this.httpOptions
		)
	};
	
	viewCategoria(id, token) {
		return this.http.post(
			this.dbUrl + 'categorias/view/' + id, 
			"authorization=" + token,
			this.httpOptions
		)
	};

	createCategoria(json, token) {
		return this.http.post(
			this.dbUrl + 'categorias/create', 
			"json="+JSON.stringify(json) + "&authorization=" + token,
			this.httpOptions
		)
	};
	
	editCategoria(json, token) {
		return this.http.post(
			this.dbUrl + 'categorias/edit', 
			"json="+JSON.stringify(json) + "&authorization=" + token,
			this.httpOptions
		)
	};

	reorderCategoria(json, token) {
		return this.http.post(
			this.dbUrl + 'categorias/reorder', 
			"json="+JSON.stringify(json) + "&authorization=" + token,
			this.httpOptions
		)
	};
	
	deleteCategoria(id, token) {
		return this.http.post(
			this.dbUrl + 'categorias/delete/' + id, 
			"authorization=" + token,
			this.httpOptions
		)
	};
	
}
