import { Component, OnInit, Input} from '@angular/core';
import {  MatDialog, MatSnackBar } from '@angular/material';
import { VarsService } from 'src/app/services/vars.service';
import { ApiService } from "../../../services/api.service";
import { ConfirmsDialog } from 'src/app/templates/confirm-dialog/confirm-dialog';
import { CreateEditCuponesDialog } from 'src/app/templates/create-edit-cupones-dialog/create-edit-cupones-dialog';
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';

export interface CuponesData {
	idcupon: number,
	descripcion: string,
	orden: number
	idempresa: object,
}
@Component({
  selector: 'app-list-cupones',
  templateUrl: './list-cupones.component.html',
  styleUrls: ['./list-cupones.component.scss']
})
export class ListCuponesComponent implements OnInit {
  // Behavior Subjects Vars
	user: object;
	
	// Subscriptions
	userSubs: any;
	
	loading: boolean = true;
	@Input() currentIdEmpresa: number;

	filesUrl = this.vars.filesUrl;
	reArranged: boolean = false;
	cupones: object[];
	
	// MatTable
	// displayedColumns: string[] = ['idcupon', 'descripcion', 'orden', 'actions'];
	// dataSource: MatTableDataSource<CuponesData>;    
	// @ViewChild(MatSort) sort: MatSort;
	// @ViewChild(MatPaginator) paginator: MatPaginator;
	
  constructor(
    private vars: VarsService,
		public api: ApiService,
		public dialog: MatDialog,
    private _snackBar: MatSnackBar,
    ) { }
    
    ngOnInit() {
			this.user = this.vars.user.source['value'];
			this.getCupones();
		}
	
		onListDrop(event: CdkDragDrop<CuponesData[]>) {
            this.reArranged = true;
            moveItemInArray(this.cupones, event.previousIndex, event.currentIndex);
        }
		
		
		getCupones(){
			this.loading = true;
			this.api.listCuponesByEmpresa(this.currentIdEmpresa, this.user['token']).subscribe(response => {
				if(response['code'] == '200'){
					this.cupones = response['data'];
				} else {
					this._snackBar.open('No se pudo listar los cupones', 'Entendido', {duration: 5000});
				}
				this.loading = false;
			});
		}
		reorder(){
            
            let cuponesOrdenados = [];
            
            this.cupones.forEach((cupon, index) => {
                let obj = {
                    idcupon: cupon['idcupon'],
                    orden: index + 1,
                }
                cuponesOrdenados.push(obj);
            });
            
            let json = {};
            json['cupones'] = JSON.stringify(cuponesOrdenados);
            
            this.loading = false;
            
            this.api.reorderCupon(json, this.user['token']).subscribe(response => {
                if (response['code'] == '200') {
					this.getCupones();
					this._snackBar.open('Los cupones furon reordenados ', 'Entendido', {duration: 5000});
                } else {
                    this._snackBar.open('No se pudo reordenar los cupones', 'Entendido', {duration: 5000});
                }
                this.reArranged = false;
            });
            
            
        }
		
		
		delete(row, idx) {
			
			
			const dialogConfigs = {
				title: 'ELIMINAR CUPN',
				subtitle: 'Desea eliminar el cupn ' + row['descripcion'] +  '?',
				done: 'Borrar',
				cancel: 'Cancelar'
			}
			
			const dialogRef = this.dialog.open(ConfirmsDialog, {
				width: '40%',
				data: dialogConfigs
			});
			
			dialogRef.afterClosed().subscribe(result => {
				
				if (result) {
					this.api.deleteCupon(row['idcupon'], this.user['token']).subscribe(response => {
						if(response['code'] == '200'){
							this.cupones.splice(idx,1);
							this._snackBar.open('El cupn fu eliminado correctamente ', 'Entendido', {duration: 5000});
							this.reorder();
						} else {
							this._snackBar.open('El cupn no pudo ser eliminado', 'Entendido', {duration: 5000});
						}
					});
					
				}
			});
		}

		CreateEditCupones(cupon){
			const dialogRef = this.dialog.open(CreateEditCuponesDialog, {
				width: '80%',
				data: {cupon: cupon, currentIdEmpresa:this.currentIdEmpresa}, 
			});
			dialogRef.afterClosed().subscribe(result => {
				if(result){
					this.getCupones();
				}
				
			});
		}
		

}
