import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { MatPaginator, MatTableDataSource, MatSort, MatDialog, MatSnackBar, MatTable } from '@angular/material';
import { VarsService } from 'src/app/services/vars.service';
import { ApiService } from "../../../services/api.service";
import { ConfirmsDialog } from 'src/app/templates/confirm-dialog/confirm-dialog';

import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';

export interface CategoriasData {
    idcategoria: number,
    nombre: string,
    orden: number,
}

@Component({
    selector: 'app-list-categorias',
    templateUrl: './list-categorias.component.html',
    styleUrls: ['./list-categorias.component.scss']
})
export class ListCategoriasComponent  implements OnInit, OnDestroy{
    
    // Behavior Subjects Vars
    user: object;
    
    // Subscriptions
    userSubs: any;
    
    loading: boolean = true;
    categorias: object[];
    filesUrl = this.vars.filesUrl;
    reArranged: boolean = false;
    
    // MatTable
    // displayedColumns: string[] = ['idcategoria', 'nombre', 'orden', 'actions'];
    // dataSource: MatTableDataSource<CategoriasData>;    
    // @ViewChild(MatSort) sort: MatSort;
    // @ViewChild(MatPaginator) paginator: MatPaginator;
    
    // @ViewChild('table') table: MatTable<CategoriasData>;
    
    constructor(
        private vars: VarsService,
        public api: ApiService,
        public dialog: MatDialog,
        private _snackBar: MatSnackBar,
        ) { }
        
        ngOnInit() {
            this.user = this.vars.user.source['value'];
            this.getCategorias();
        }
        
        onListDrop(event: CdkDragDrop<CategoriasData[]>) {
            this.reArranged = true;
            moveItemInArray(this.categorias, event.previousIndex, event.currentIndex);
        }
        
        
        getCategorias(){
            this.loading = true;
            this.api.listCategorias(this.user['token']).subscribe(response => {
                if(response['code'] == '200'){
                    this.categorias = response['data'];
                } else {
                    this._snackBar.open('No se pudo listar las categoras', 'Entendido', {duration: 5000});
                }
                this.loading = false;
            });
        }
        
        
        reorder(){
            
            let categoriasOrdenadas = [];
            
            this.categorias.forEach((categoria, index) => {
                let obj = {
                    idcategoria: categoria['idcategoria'],
                    orden: index + 1,
                }
                categoriasOrdenadas.push(obj);
            });

            
            let json = {};
            json['categorias'] = JSON.stringify(categoriasOrdenadas);
            
            this.loading = false;
            
            this.api.reorderCategoria(json, this.user['token']).subscribe(response => {
                if (response['code'] == '200') {
                    this.getCategorias();
                    this._snackBar.open('Categoras reordenadas', 'Entendido', {duration: 5000});

                } else {
                    this._snackBar.open('No se pudo reordenar las categoras', 'Entendido', {duration: 5000});
                }
                this.reArranged = false;
            });
            
            
        }
        
        
        delete(row, idx) {
            
            const dialogConfigs = {
                title: 'ELIMINAR CATEGORA',
                subtitle: 'Desea eliminar la categora ' + row['nombre'] +  '?',
                done: 'Borrar',
                cancel: 'Cancelar'
            }
            
            const dialogRef = this.dialog.open(ConfirmsDialog, {
                width: '40%',
                data: dialogConfigs
            });
            
            dialogRef.afterClosed().subscribe(result => {
                
                if (result) {
                    this.api.deleteCategoria(row['idcategoria'], this.user['token']).subscribe(response => {
                        if(response['code'] == '200'){
                            this.categorias.splice(idx,1);
                            this._snackBar.open('La categora fu eliminada correctamente ', 'Entendido', {duration: 5000});
                            this.reorder();
                        } else if(response['code'] == '401'){
                            this._snackBar.open('No se pudo eliminar la categora porque est asociada a una empresa', 'Entendido', {duration: 5000});
                        } else {
                            this._snackBar.open('La categora no pudo ser eliminada', 'Entendido', {duration: 5000});
                        }
                    });
                    
                }
            });
        }
        
        ngOnDestroy() {
            
        }
        
    }
    