import { Component, OnInit } from '@angular/core';
import { VarsService } from 'src/app/services/vars.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from "../../../services/api.service";
import { Router, ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material';

@Component({
	selector: 'app-create-edit-user',
	templateUrl: './create-edit-user.component.html',
	styleUrls: ['./create-edit-user.component.scss']
})
export class CreateEditUserComponent implements OnInit {
	
	// Behavior Subjects Vars
	user: object;
	// Forms
	form: FormGroup;
	// Vars
	error: string;
	loading: boolean = false;
	load: boolean = false;
	roles: object[] = this.vars.roles;
	currentIdUser: number;
	
	
	constructor(
		private vars: VarsService,
		public api: ApiService,
		private _formBuilder: FormBuilder,
		private _router: Router,
		private _snackBar: MatSnackBar,
		private _route: ActivatedRoute
		) { }
		
		ngOnInit() {
			
			this.user = this.vars.user.source['value'];
			this.currentIdUser = this._route.snapshot.params.id;
			
			this.form = this._formBuilder.group({
				idusuario: [''],
				email: ['', [Validators.required, Validators.email, Validators.maxLength(80) ]],
				password: ['', Validators.required],
				origen: ['', Validators.maxLength(30)],
				rol: ['', Validators.required],
			});
			
			if(this.currentIdUser){

				this.load = true;
				
				this.api.viewUsuario(this.currentIdUser, this.user['token']).subscribe(response => {       
					if(response['code'] == '200'){
						this.form.patchValue(response['data']);
						this.form.controls.email.disable();
						this.form.controls.password.setValidators(null);
						this.form.controls.password.setValue(null);
					} else {
						this._snackBar.open('Usuario no encontrado', 'Entendido', {duration: 5000});
						this._router.navigate(['/usuarios'])
					}
					this.load = false;
				});
			}
			
			
		}
		
		creatEdit() {
			
			this.error = "";
			this.loading = true;

			let json = JSON.parse(JSON.stringify(this.form.value));
			
			if(this.currentIdUser){
				this.api.editUsuario(json, this.user['token']).subscribe(response => {     
					   
					if(response['code'] == '200'){
						this._snackBar.open('Usuario editado', 'Entendido', {duration: 5000});
						this._router.navigate(['/usuarios'])
					}else if(response['code'] == '401'){
						this.error = "Email duplicado.";
					} else {
						this.error = "El usuario no pudo ser editado.";
					}
					this.loading = false;
				});
			}else{
				this.api.createUsuario(json, this.user['token']).subscribe(response => {     
					    
					if(response['code'] == '200'){
						this._snackBar.open('Usuario creado', 'Entendido', {duration: 5000});
						this._router.navigate(['/usuarios'])
					}else if(response['code'] == '401'){
						this.error = "Email duplicado.";
					} else {
						this.error = "El usuario no pudo ser creado.";
					}
					this.loading = false;
				});
			}
		}
		
	}
	
	
	