import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatTableDataSource, MatSort, MatDialog, MatSnackBar } from '@angular/material';
import { VarsService } from 'src/app/services/vars.service';
import { ApiService } from "../../../services/api.service";
import { ConfirmsDialog } from 'src/app/templates/confirm-dialog/confirm-dialog';

export interface UsuariosData {
  idempresa: number,
  nombre: string,
  email: string,
  redes: string,
  descripcion: string,
  keywords: string,
  idusuario: object,
  idcategoria: object,
  estado: boolean,
}

@Component({
  selector: 'app-list-empresas',
  templateUrl: './list-empresas.component.html',
  styleUrls: ['./list-empresas.component.scss']
})
export class ListEmpresasComponent implements OnInit {

  // Behavior Subjects Vars
  user: object;
    
  // Subscriptions
  userSubs: any;

  loading: boolean = true;
  
  // MatTable
  displayedColumns: string[] = ['estado','idempresa', 'nombre', 'email', 'idusuario', 'idcategoria', 'actions'];
  dataSource: MatTableDataSource<UsuariosData>;    
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  
  constructor(
      private vars: VarsService,
      public api: ApiService,
      public dialog: MatDialog,
      private _snackBar: MatSnackBar,
      ) { }
      
      ngOnInit() {
          this.user = this.vars.user.source['value'];
          this.getEmpresas();
      }


      getEmpresas(){
        this.loading = true;
        this.api.listEmpresas(this.user['token']).subscribe(response => {
            if(response['code'] == '200'){
                
                this.dataSource = new MatTableDataSource(response['data']);
                this.dataSource.paginator = this.paginator;
                this.dataSource.sort = this.sort;
            } else {
                this._snackBar.open('No se pudo listar las empresas', 'Entendido', {duration: 5000});
            }
            this.loading = false;
        });
    }
         

      applyFilter(filterValue: string) {
          this.dataSource.filter = filterValue.trim().toLowerCase();
          if (this.dataSource.paginator) {
              this.dataSource.paginator.firstPage();
          }
      }
      

      delete(row) {

          

          const dialogConfigs = {
              title: 'ELIMINAR EMPRESA',
              subtitle: 'Desea eliminar la empresa ' + row['nombre'] +  '?',
              done: 'Borrar',
              cancel: 'Cancelar'
          }
          
          const dialogRef = this.dialog.open(ConfirmsDialog, {
              width: '40%',
              data: dialogConfigs
          });
          
          dialogRef.afterClosed().subscribe(result => {

              if (result) {
                  this.api.deleteEmpresa(row['idempresa'], this.user['token']).subscribe(response => {
                      if(response['code'] == '200'){
                          this._snackBar.open('La empresa fu eliminada correctamente ', 'Entendido', {duration: 5000});
                          this.getEmpresas();
                      } else {
                          this._snackBar.open('La empresa no pudo ser eliminada', 'Entendido', {duration: 5000});
                      }
                  });

              }
          });
      }
      

}
