import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from "./../../services/api.service";
import { VarsService } from 'src/app/services/vars.service';
import { MatSnackBar, MatChipInputEvent } from '@angular/material';
import {COMMA, ENTER} from '@angular/cdk/keycodes';

@Component({
    selector: 'create-edit-cupones-dialog',
    templateUrl: './create-edit-cupones-dialog.html',
    styleUrls: ['./create-edit-cupones-dialog.scss'],
})
export class CreateEditCuponesDialog{
    
    // Behavior Subjects Vars
    user: object;
    // Forms
    form: FormGroup;
    // Vars
    loading: boolean;
    error: string;
    
    filesUrl = this.vars.filesUrl;
    
    // Ships
    readonly separatorKeysCodes: number[] = [ENTER, COMMA];
    
    constructor(
        private vars: VarsService,
        public api: ApiService,
        private _formBuilder: FormBuilder,
        private _snackBar: MatSnackBar,
        public dialogRef: MatDialogRef<CreateEditCuponesDialog>,
        @Inject(MAT_DIALOG_DATA) public data
        
        ) {
            
            this.user = this.vars.user.source['value'];
            
            this.form = this._formBuilder.group({
                idcupon: [''],
                descripcion: ['', [Validators.required, Validators.maxLength(300) ]],
                keywords: [[]],
                idempresa: [data.currentIdEmpresa],
                imagen: [''],
                image: [''],
            });
            
            
            if(this.data.cupon){
                data.cupon.keywords = data.cupon.keywords ? JSON.parse(data.cupon.keywords) : [];
                this.form.patchValue(data.cupon);
                
            }
        }
        
        
        cancel(): void {
            this.dialogRef.close();
        }
        
        
        onFileChange(ev){
            if(ev.target.files[0]){
                
                var reader = new FileReader();
                reader.readAsDataURL(ev.target.files[0]);
                reader.onload = (e) => {
                    this.form.controls.imagen.setValue(reader.result);
                }
            }
        }
        
        
        creatEdit(){
            this.error = "";
            this.loading =true;
            let json = JSON.parse(JSON.stringify(this.form.value));
            json['keywords'] = JSON.stringify(json['keywords']);
            
            if(this.data.cupon){
                this.api.editCupon(json, this.user['token']).subscribe(response => {      
                    if(response['code'] == '200'){ 
                             
                        this._snackBar.open('Cupn editado', 'Entendido', {duration: 5000});
                        this.dialogRef.close(true);
                    }else if(response['code'] == '401'){
                        this._snackBar.open('El cupn no existe', 'Entendido', {duration: 5000});
                    }else if(response['code'] == '402'){
                        this._snackBar.open('Parametros incompletos', 'Entendido', {duration: 5000});
                    } else {
                        this.error = "El Cupn  no pudo ser editado.";
                    }
                    this.loading = false;
                });
                
            }else{
                
                this.api.createCupon(json, this.user['token']).subscribe(response => {       
                    if(response['code'] == '200'){
                        this._snackBar.open('Cupn creado', 'Entendido', {duration: 5000});
                        this.dialogRef.close(true);
                    } else {
                        this.error = "El Cupn no pudo ser creado.";
                    }
                    this.loading = false;
                });
                
            }
        }
        
        
        addKeyword(event: MatChipInputEvent): void {
            const input = event.input;
            const value = event.value;
            let kw = this.form.controls.keywords.value ? this.form.controls.keywords.value : [];
            
            if ((value || '').trim()) { 
                kw.push(value.trim());
                this.form.controls.keywords.setValue(kw);
            }
            if (input) { input.value = ''; }
        }
        
        
        removeKeyword(fruit): void {
            let kw = this.form.controls.keywords.value;
            const index = kw.indexOf(fruit);
            if (index >= 0) {
                kw.splice(index, 1);
                this.form.controls.keywords.setValue(kw);
            }
        }
        
    }