import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatTableDataSource, MatSort, MatDialog, MatSnackBar } from '@angular/material';
import { VarsService } from 'src/app/services/vars.service';
import { ApiService } from "../../../services/api.service";
import { ConfirmsDialog } from 'src/app/templates/confirm-dialog/confirm-dialog';

export interface UsuariosData {
    idusuario: number,
    email: string,
    rol: number,
    origen: string,
}

@Component({
    selector: 'app-list-users',
    templateUrl: './list-users.component.html',
    styleUrls: ['./list-users.component.scss']
})
export class ListUsersComponent implements OnInit {
    
    // Behavior Subjects Vars
    user: object;
    
    // Subscriptions
    userSubs: any;

    loading: boolean = true;
    roles: object[] = this.vars.roles;
    
    // MatTable
    displayedColumns: string[] = ['idusuario', 'email', 'rol', 'origen', 'actions'];
    dataSource: MatTableDataSource<UsuariosData>;    
    @ViewChild(MatSort, { static: true }) sort: MatSort;
    @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
    
    constructor(
        private vars: VarsService,
        public api: ApiService,
        public dialog: MatDialog,
        private _snackBar: MatSnackBar,
        ) { }
        
        ngOnInit() {
            this.user = this.vars.user.source['value'];
            this.getUsuarios();
        }


        getUsuarios(){
            this.loading = true;
            this.api.listUsuarios(this.user['token']).subscribe(response => {
                if(response['code'] == '200'){
                    this.dataSource = new MatTableDataSource(response['data']);
                    this.dataSource.paginator = this.paginator;
                    this.dataSource.sort = this.sort;
                } else {
                    this._snackBar.open('No se pudo listar los usuarios', 'Entendido', {duration: 5000});
                }
                this.loading = false;
            });
        }
           

        applyFilter(filterValue: string) {
            this.dataSource.filter = filterValue.trim().toLowerCase();
            if (this.dataSource.paginator) {
                this.dataSource.paginator.firstPage();
            }
        }
        

        delete(row) {


            const dialogConfigs = {
                title: 'ELIMINAR USUARIO',
                subtitle: 'Desea eliminar el usuario ' + row['email'] +  '?',
                done: 'Borrar',
                cancel: 'Cancelar'
            }
            
            const dialogRef = this.dialog.open(ConfirmsDialog, {
                width: '40%',
                data: dialogConfigs
            });
            
            dialogRef.afterClosed().subscribe(result => {

                if (result) {
                    this.api.deleteUsuario(row['idusuario'], this.user['token']).subscribe(response => {
                        if(response['code'] == '200'){
                            this._snackBar.open('El usuario fu eliminado correctamente ', 'Entendido', {duration: 5000});
                            this.getUsuarios();
                        } else {
                            this._snackBar.open('El usuario no pudo ser eliminado', 'Entendido', {duration: 5000});
                        }
                    });

                }
            });
        }
  
    }
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    