import { Component, OnInit, ViewChild, Input} from '@angular/core';
import { MatPaginator, MatTableDataSource, MatSort, MatDialog, MatSnackBar } from '@angular/material';
import { VarsService } from 'src/app/services/vars.service';
import { ApiService } from "../../../services/api.service";
import { ConfirmsDialog } from 'src/app/templates/confirm-dialog/confirm-dialog';
import { CreateEditDirecionesDialog } from 'src/app/templates/create-edit-direciones-dialog/create-edit-direciones-dialog';

export interface UsuariosData {
	iddireccion: number,
	descripcion: string,
	telefonos: string,
	ciudad: string,
	idempresa: object,
}

@Component({
	selector: 'app-list-direcciones',
	templateUrl: './list-direcciones.component.html',
	styleUrls: ['./list-direcciones.component.scss']
})
export class ListDireccionesComponent implements OnInit {
	// Behavior Subjects Vars
	user: object;
	
	// Subscriptions
	userSubs: any;
	
	loading: boolean = true;
	@Input() currentIdEmpresa: number;
	
	// MatTable
	displayedColumns: string[] = ['iddireccion', 'descripcion', 'telefonos', 'ciudad', 'actions'];
	dataSource: MatTableDataSource<UsuariosData>;    
	@ViewChild(MatSort, { static: true }) sort: MatSort;
	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	
	constructor(
		private vars: VarsService,
		public api: ApiService,
		public dialog: MatDialog,
		private _snackBar: MatSnackBar,
		) { }
		
		ngOnInit() {
			this.user = this.vars.user.source['value'];
			this.getDirecciones();
		}
		
		
		getDirecciones(){
			this.loading = true;
			this.api.listDireccionesByEmpresa(this.currentIdEmpresa, this.user['token']).subscribe(response => {
				if(response['code'] == '200'){
					this.dataSource = new MatTableDataSource(response['data']);
					this.dataSource.paginator = this.paginator;
					this.dataSource.sort = this.sort;
				} else {
					this._snackBar.open('No se pudo listar las direcciones', 'Entendido', {duration: 5000});
				}
				this.loading = false;
			});
		}
		
		
		applyFilter(filterValue: string) {
			this.dataSource.filter = filterValue.trim().toLowerCase();
			if (this.dataSource.paginator) {
				this.dataSource.paginator.firstPage();
			}
		}
		
		
		delete(row) {
						
			const dialogConfigs = {
				title: 'ELIMINAR DIRECCIN',
				subtitle: 'Desea eliminar la direccin ' + row['descripcion'] +  '?',
				done: 'Borrar',
				cancel: 'Cancelar'
			}
			
			const dialogRef = this.dialog.open(ConfirmsDialog, {
				width: '40%',
				data: dialogConfigs
			});
			
			dialogRef.afterClosed().subscribe(result => {
				
				if (result) {
					this.api.deleteDireccion(row['iddireccion'], this.user['token']).subscribe(response => {
						if(response['code'] == '200'){
							this._snackBar.open('La direccin fu eliminada correctamente ', 'Entendido', {duration: 5000});
							this.getDirecciones();
						} else {
							this._snackBar.open('La direccin no pudo ser eliminada', 'Entendido', {duration: 5000});
						}
					});
					
				}
			});
		}

		CreateEditDireciones(direccion){
			const dialogRef = this.dialog.open(CreateEditDirecionesDialog, {
				width: '80%',
				data: {direccion: direccion, currentIdEmpresa:this.currentIdEmpresa}, 
			});
			dialogRef.afterClosed().subscribe(result => {
				if(result){
					this.getDirecciones();
				}
				
			});
		}
		
	}
	