import { Component, OnInit } from '@angular/core';
import { VarsService } from 'src/app/services/vars.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from "../../../services/api.service";
import { Router, ActivatedRoute } from '@angular/router';
import { MatSnackBar, MatChipInputEvent } from '@angular/material';
import {COMMA, ENTER} from '@angular/cdk/keycodes';

@Component({
	selector: 'app-create-edit-empresas',
	templateUrl: './create-edit-empresas.component.html',
	styleUrls: ['./create-edit-empresas.component.scss']
})
export class CreateEditEmpresasComponent implements OnInit {
	
	// Behavior Subjects Vars
	user: object;
	// Forms
	form: FormGroup;
	// Vars
	error: string;
	loading: boolean = false;
	load: boolean = false;
	currentIdEmpresa: number;
	
	usuarios: object[];
	categorias: object[];
	// listRedes = this.vars.redes;
	
	// Ships
	readonly separatorKeysCodes: number[] = [ENTER, COMMA];
	
	
	redes: object = {
		facebook: "",
		instagram: "",
		linkedin: "",
		snapchat: "",
		twitter: "",
		whatsapp: "",
	}
	
	
	constructor(
		private vars: VarsService,
		public api: ApiService,
		private _formBuilder: FormBuilder,
		private _router: Router,
		private _snackBar: MatSnackBar,
		private _route: ActivatedRoute
		) { }
		
		ngOnInit() {
			this.user = this.vars.user.source['value'];
			this.currentIdEmpresa = this._route.snapshot.params.id;
			
			this.form = this._formBuilder.group({
				idempresa: [''],
				estado: [true],
				nombre: ['', [Validators.required, Validators.maxLength(100)]],
				email: ['', [Validators.required, Validators.email, Validators.maxLength(80) ]],
				descripcion: ['', Validators.maxLength(300)],
				keywords: [[]],
				idusuario: ['', Validators.required],
				idcategoria: ['', Validators.required],	
			});
			
			this.form.controls.idusuario.disable();
			this.form.controls.idcategoria.disable();
			
			this.api.listUsuarios(this.user['token']).subscribe(response => {
				if (response['code'] == '200') {
					this.usuarios = response['data'];
					this.form.controls.idusuario.enable();
				} else {
					this._snackBar.open('No se pudo cargar los usuarios', 'Entendido', {duration: 5000});
					this._router.navigate(['/empresas'])
				}
			});
			
			this.api.listCategorias(this.user['token']).subscribe(response => {
				if (response['code'] == '200') {
					this.categorias = response['data'];
					this.form.controls.idcategoria.enable();
				} else {
					this._snackBar.open('No se pudo cargar las categoras', 'Entendido', {duration: 5000});
					this._router.navigate(['/empresas'])
				}
			});
			
			
			if(this.currentIdEmpresa){
				
				this.load = true;
				
				this.api.viewEmpresa(this.currentIdEmpresa, this.user['token']).subscribe(response => {     
					 
					if(response['code'] == '200'){
						response['data'].idcategoria = response['data'].idcategoria.idcategoria;
						response['data'].idusuario = response['data'].idusuario.idusuario;
						response['data'].keywords = response['data'].keywords ? JSON.parse(response['data'].keywords) : [];
						this.redes = JSON.parse(response['data'].redes)
						this.form.patchValue(response['data']);
						this.form.controls.email.disable();
					} else {
						this._snackBar.open('Empresa no encontrada', 'Entendido', {duration: 5000});
						this._router.navigate(['/empresas'])
					}
					this.load = false;
				});
			}
			
			
		}
		
		creatEdit() {
			
			this.error = "";
			this.loading = true;
			
			let json = JSON.parse(JSON.stringify(this.form.value));
			
			json['redes'] = JSON.stringify(this.redes);
			json['keywords'] = JSON.stringify(json['keywords']);
			
			if(this.currentIdEmpresa){
				this.api.editEmpresa(json, this.user['token']).subscribe(response => {     
				    
					if(response['code'] == '200'){
						this._snackBar.open('Empresa editada', 'Entendido', {duration: 5000});
						this._router.navigate(['/empresas'])
					} else {
						this.error = "La empresa  no pudo ser editada.";
					}
					this.loading = false;
				});
			}else{
				this.api.createEmpresa(json, this.user['token']).subscribe(response => {     
					   
					if(response['code'] == '200'){
						this._snackBar.open('Empresa creada', 'Entendido', {duration: 5000});
						this._router.navigate(['/empresas'])
					}else if(response['code'] == '401'){
						this.error = "Email duplicado.";
					} else {
						this.error = "La empresa no pudo ser creada.";
					}
					this.loading = false;
				});
			}
		}
		
		addKeyword(event: MatChipInputEvent): void {
			const input = event.input;
			const value = event.value;
			let kw = this.form.controls.keywords.value ? this.form.controls.keywords.value : [];
			
			if ((value || '').trim()) { 
				kw.push(value.trim());
				this.form.controls.keywords.setValue(kw);
			}
			if (input) { input.value = ''; }
		}
		
		removeKeyword(fruit): void {
			let kw = this.form.controls.keywords.value;
			const index = kw.indexOf(fruit);
			if (index >= 0) {
				kw.splice(index, 1);
				this.form.controls.keywords.setValue(kw);
			}
		}
		
	}
	