import { Component, OnInit } from '@angular/core';
import { VarsService } from 'src/app/services/vars.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from "../../../services/api.service";
import { Router, ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material';

@Component({
	selector: 'app-create-edit-categoria',
	templateUrl: './create-edit-categoria.component.html',
	styleUrls: ['./create-edit-categoria.component.scss']
})
export class CreateEditCategoriaComponent implements OnInit {
	
	// Behavior Subjects Vars
	user: object;
	// Forms
	form: FormGroup;
	// Vars
	error: string;
	loading: boolean = false;
	load: boolean = false;
	currentIdCategoria: number;

	filesUrl = this.vars.filesUrl;
		
	
	constructor(
		private vars: VarsService,
		public api: ApiService,
		private _formBuilder: FormBuilder,
		private _router: Router,
		private _snackBar: MatSnackBar,
		private _route: ActivatedRoute
		) { }
		

		ngOnInit() {
			
			this.user = this.vars.user.source['value'];
			this.currentIdCategoria = this._route.snapshot.params.id;
			
			this.form = this._formBuilder.group({
				idcategoria: [''],
				nombre: ['', [Validators.required, Validators.maxLength(100) ]],
				imagen: [''],
				image: [''],
			});
			
			if(this.currentIdCategoria){
				this.load = true;
				this.api.viewCategoria(this.currentIdCategoria, this.user['token']).subscribe(response => {       
					if(response['code'] == '200'){
						this.form.patchValue(response['data']);
					} else {
						this._snackBar.open('Categora no encontrada', 'Entendido', {duration: 5000});
						this._router.navigate(['/categorias'])
					}
					this.load = false;
				});
			}			
		}


		onFileChange(ev){
			if(ev.target.files[0]){
				var reader = new FileReader();
				reader.readAsDataURL(ev.target.files[0]);
				reader.onload = (e) => {
					this.form.controls.imagen.setValue(reader.result);
				}
			}
		}
		

		creatEdit() {
			
			this.error = "";
			this.loading = true;

			let json = JSON.parse(JSON.stringify(this.form.value));
			
			if(this.currentIdCategoria){

				this.api.editCategoria(json, this.user['token']).subscribe(response => {        
					if(response['code'] == '200'){
						this._snackBar.open('Categora editada', 'Entendido', {duration: 5000});
						this._router.navigate(['/categorias'])
					} else {
						this.error = "La categora no pudo ser editada.";
					}
					this.loading = false;
				});

				
			}else{
				this.api.createCategoria(json, this.user['token']).subscribe(response => {       
					if(response['code'] == '200'){
						this._snackBar.open('Categora creada', 'Entendido', {duration: 5000});
						this._router.navigate(['/categorias'])
					} else {
						this.error = "La categora no pudo ser creada.";
					}
					this.loading = false;
				});
			}
		}

		
		
	}
	