import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';;
import { ListUsersComponent } from './pages/users/list-users/list-users.component';
import { CreateEditUserComponent } from './pages/users/create-edit-user/create-edit-user.component';
import { ListCategoriasComponent } from './pages/categories/list-categorias/list-categorias.component';
import { CreateEditCategoriaComponent } from './pages/categories/create-edit-categoria/create-edit-categoria.component';
import { ListEmpresasComponent } from './pages/empresas/list-empresas/list-empresas.component';
import { CreateEditEmpresasComponent } from './pages/empresas/create-edit-empresas/create-edit-empresas.component';


const routes: Routes = [
  // Default
  {
    path: '',
    component: ListUsersComponent
  },
  //usuarios
  {
    path: 'usuarios',
    component: ListUsersComponent
  },
  {
    path: 'usuarios/crear',
    component: CreateEditUserComponent
  },
  {
    path: 'usuarios/editar/:id',
    component: CreateEditUserComponent
  },
  //categorias
  {
    path: 'categorias',
    component: ListCategoriasComponent
  },
  {
    path: 'categorias/crear',
    component: CreateEditCategoriaComponent
  },
  {
    path: 'categorias/editar/:id',
    component: CreateEditCategoriaComponent
  },
  //empresas
  {
    path: 'empresas',
    component: ListEmpresasComponent
  },
  {
    path: 'empresas/crear',
    component: CreateEditEmpresasComponent
  },
  {
    path: 'empresas/editar/:id',
    component: CreateEditEmpresasComponent
  },
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
