import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from "./../../services/api.service";
import { VarsService } from 'src/app/services/vars.service';
import { MatSnackBar } from '@angular/material';

@Component({
    selector: 'create-edit-direciones-dialog',
    templateUrl: './create-edit-direciones-dialog.html',
})
export class CreateEditDirecionesDialog{
    
    // Behavior Subjects Vars
    user: object;
    // Forms
    form: FormGroup;
    // Vars
    loading: boolean;
    load: boolean;
    error: string;
    
    
    
    constructor(
        private vars: VarsService,
        public api: ApiService,
        private _formBuilder: FormBuilder,
        private _snackBar: MatSnackBar,
        public dialogRef: MatDialogRef<CreateEditDirecionesDialog>,
        @Inject(MAT_DIALOG_DATA) public data
        
        ) {
            
            this.user = this.vars.user.source['value'];
            
            this.form = this._formBuilder.group({
                iddireccion: [''],
                descripcion: ['', [Validators.required, Validators.maxLength(300) ]],
                telefonos: ['',  Validators.maxLength(50)],
                ciudad: ['', Validators.maxLength(30)],
                idempresa: [data.currentIdEmpresa],
                latitudmap: [''],
                longuitudmap: [''],
            });
            
            if(this.data.direccion){
                
                data.direccion.latitudmap = parseFloat(data.direccion.latitudmap);
                data.direccion.longuitudmap = parseFloat(data.direccion.longuitudmap);

                this.form.patchValue(data.direccion);

            } else {
                this.api.getInfoUser().subscribe(response => {                    
                    if(response['latitude'] && response['longitude']){
                        this.form.controls.latitudmap.setValue(response['latitude']);
                        this.form.controls.longuitudmap.setValue(response['longitude']);
                    }
                });
            }
        }
        
        cancel(): void {
            this.dialogRef.close();
        }
        
        onChoseLocation(event){
            
            this.form.controls.latitudmap.setValue(event.coords.lat);
            this.form.controls.longuitudmap.setValue(event.coords.lng);
            
        }
        
        creatEdit(){
            this.error = "";
            this.loading =true;
            this.load =true;
            let json = JSON.parse(JSON.stringify(this.form.value));
            
            if(this.data.direccion){
                this.api.editDireccion(json, this.user['token']).subscribe(response => {     
                       
                    if(response['code'] == '200'){
                        this._snackBar.open('Direccin editada', 'Entendido', {duration: 5000});
                        this.dialogRef.close(true);
                    } else {
                        this.error = "La direccin  no pudo ser editada.";
                    }
                    this.loading = false;
                    this.load = false;
                });
                
            }else{
                
                this.api.createDireccion(json, this.user['token']).subscribe(response => {     
                        
                    if(response['code'] == '200'){
                        this._snackBar.open('Direccin creada', 'Entendido', {duration: 5000});
                        this.dialogRef.close(true);
                    } else {
                        this.error = "La Direccin no pudo ser creada.";
                    }
                    this.loading = false;
                });
                
            }
            
            
        }
        
    }